// app/countries/loading.tsx
export default function Loading() {
  // Assume the table has 9 columns and we want 5 skeleton rows
  const columnsCount = 9;
  const rowsCount = 5;
  const columns = Array.from({ length: columnsCount }, (_, i) => i);
  const rows = Array.from({ length: rowsCount }, (_, i) => i);

  return (
    <div className="min-h-screen p-8 flex flex-col items-center justify-center">
      <div className="w-full max-w-7xl animate-pulse">
        {/* Skeleton for the Title */}
        <div className="h-8 bg-gray-300 rounded w-1/2 mx-auto mb-6"></div>

        {/* Skeleton for the Filter Form */}
        <div className="mb-6 space-y-4">
          <div className="h-10 bg-gray-300 rounded w-full"></div>
          <div className="h-10 bg-gray-300 rounded w-full"></div>
        </div>

        {/* Skeleton Table */}
        <div className="overflow-x-auto">
          <table className="min-w-full border border-gray-200">
            <thead className="bg-gray-200">
            <tr>
              {columns.map((col) => (
                <th key={col} className="p-2 border">
                  <div className="h-4 bg-gray-400 rounded"></div>
                </th>
              ))}
            </tr>
            </thead>
            <tbody>
            {rows.map((row) => (
              <tr key={row} className="text-center">
                {columns.map((col) => (
                  <td key={col} className="p-2 border whitespace-pre-line">
                    <div className="h-4 bg-gray-400 rounded"></div>
                  </td>
                ))}
              </tr>
            ))}
            </tbody>
          </table>
        </div>

        {/* Skeleton for Pagination */}
        <div className="mt-6 w-1/3 mx-auto">
          <div className="h-8 bg-gray-300 rounded"></div>
        </div>
      </div>
    </div>
  );
}
